*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 10px;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.5rem;
  line-height: 1.6;
  font-weight: normal;
  color: #444;
}

h1 {
  font-size: 2.7rem;
  font-weight: bold;
  line-height: 1.11;
  color: #000;
}

@media (min-width: 768px) {
  h1 {
    font-size: 4.3rem;
    line-height: 1.17;
  }
}
.page-wrapper {
  position: relative;
  width: 100%;
  max-width: 1250px;
  min-width: 320px;
  margin-left: auto;
  margin-right: auto;
  background-color: #fff;
}

.container {
  padding-left: 30px;
  padding-right: 30px;
  margin-left: auto;
  margin-right: auto;
  max-width: 1150px;
}

@media all and (min-width: 768px) {
  .container {
    padding-left: 50px;
    padding-right: 50px;
  }
}
.block-title {
  font-size: 2.5rem;
  font-weight: bold;
  line-height: 1.2;
  margin-bottom: 0.6em;
  color: #000;
}

@media (min-width: 768px) {
  .block-title {
    font-size: 4.3rem;
    line-height: 1.16;
    margin-bottom: 60px;
  }
}
.block-subtitle {
  font-weight: bold;
  color: #63d1bb;
  font-size: 1.7rem;
  line-height: 1.2;
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .block-subtitle {
    max-width: 85px;
    text-align: center;
    line-height: 1;
  }
}
.header {
  padding-top: 90px;
}

.header__top {
  border-bottom: 1px solid #ececec;
  padding-top: 20px;
  padding-bottom: 20px;
  background: #fff;
  z-index: 9999;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 90px;
}

.header__content {
  min-height: 300px;
  background: url(../images/header/hero-mobile.jpg) no-repeat top/cover;
  padding-top: 75px;
}

.header__title {
  text-align: center;
}

.header__button-wrap {
  margin-top: -20px;
}

.header__button {
  display: block;
  position: relative;
  width: 100%;
  height: 100px;
  text-align: left;
  padding-left: 30px;
  padding-right: 30px;
  border: none;
  outline: none;
  cursor: pointer;
}
.header__button span {
  display: block;
  width: 130px;
  color: #fff;
  font-size: 1.9rem;
  line-height: 1.26;
  font-weight: bold;
  font-family: inherit;
}

.header__button--light {
  background: #63d1bb;
}
.header__button--light::after {
  opacity: 0.5;
  content: "";
  display: block;
  position: absolute;
  right: 40px;
  top: 0px;
  bottom: 0px;
  width: 60px;
  background: url(../images/header/interior.svg) no-repeat center;
}
.header__button--light:hover, .header__button--light:focus {
  background: #59d5bc;
}
.header__button--light:hover::after, .header__button--light:focus::after {
  opacity: 1;
}
.header__button--light:active::after {
  opacity: 0.3;
}
.header__button--light:active span {
  color: rgba(255, 255, 255, 0.5);
}

.header__button--dark {
  background: #56c3b2;
}
.header__button--dark::after {
  opacity: 0.5;
  content: "";
  display: block;
  position: absolute;
  right: 40px;
  top: 0px;
  bottom: 0px;
  width: 60px;
  background: url(../images/header/toy-svg.svg) no-repeat center;
}
.header__button--dark:hover, .header__button--dark:focus {
  background: #46c1ae;
}
.header__button--dark:active::after {
  opacity: 0.3;
}
.header__button--dark:active span {
  color: rgba(255, 255, 255, 0.5);
}

@media (min-width: 768px) {
  .header {
    padding-top: 80px;
  }

  .header__top {
    position: absolute;
    padding-top: 0px;
    padding-bottom: 0px;
    height: auto;
  }

  .header__content {
    position: relative;
    min-height: 630px;
    background: url(../images/header/hero-desktop.jpg) no-repeat;
    background-position: center center;
    background-size: cover;
  }

  .header__title {
    width: 14em;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .header__button-wrap {
    position: relative;
    display: flex;
    padding-left: 50px;
    padding-right: 50px;
  }
  .header__button-wrap::after {
    display: block;
    position: absolute;
    left: 0px;
    bottom: -15px;
    width: 100%;
    height: 25px;
    content: "";
    background-image: url(../images/header/zigzag-fill-desktop.svg);
    background-repeat: repeat-x;
  }
}
.logo {
  text-align: center;
}

.logo__link {
  display: inline-block;
  width: 90px;
  height: 40px;
  height: auto;
  position: relative;
  overflow: hidden;
  transition: all 0.3s;
}
.logo__link:hover, .logo__link:focus {
  opacity: 0.5;
}
.logo__link:active {
  opacity: 0.2;
}

.logo__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.logo--footer {
  display: none;
}

@media (min-width: 768px) {
  .logo {
    position: absolute;
    left: 50px;
    top: 50%;
    transform: translateY(-50%);
  }

  .logo__link {
    width: 115px;
    height: 50px;
  }

  .logo--footer {
    display: block;
  }
}
.main-nav {
  position: fixed;
  top: 90px;
  left: 0px;
  width: 100%;
  height: auto;
}

.main-nav__btn {
  position: fixed;
  top: 35px;
  right: 30px;
  width: 20px;
  height: 20px;
  background: transparent;
  border: none;
}
.main-nav__btn span {
  display: block;
  height: 2px;
  margin-bottom: 4px;
  background-color: #000;
  transition: all 0.3s;
}

.main-nav__btn.open span:nth-child(2) {
  display: none;
}
.main-nav__btn.open span:first-child {
  transform: rotate(405deg);
}
.main-nav__btn.open span:last-child {
  transform: rotate(-405deg) translate(4px, -4px);
}

.main-nav__list {
  display: none;
  opacity: 0;
  height: 0px;
  overflow: hidden;
  list-style: none;
  background: #fff;
}

.main-nav__list.main-nav__list--show {
  animation: hide-menu 0.4s linear forwards;
  display: block;
}

@keyframes hide-menu {
  0% {
    height: 0px;
    opacity: 0;
  }
  100% {
    height: auto;
    opacity: 1;
  }
}
.main-nav__item {
  position: relative;
  padding: 30px;
  border-bottom: 1px solid #ececec;
  height: 80px;
}

.main-nav__link {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  text-decoration: none;
  text-align: center;
  line-height: 80px;
  color: #000;
  font-size: 1.7rem;
  font-weight: 600;
  transition: all 0.3s;
}
.main-nav__link:hover, .main-nav__link:focus {
  color: #9f9f9f;
}
.main-nav__link:active {
  color: #bdbdbd;
}

.main-nav__link--search::before {
  content: "";
  position: absolute;
  left: 30px;
  top: 0px;
  bottom: 0px;
  width: 20px;
  background: url(../images/main-nav/search.svg) no-repeat center/contain;
  filter: grayscale(100%);
}

.main-nav__link--bucket::before {
  content: "";
  position: absolute;
  left: 30px;
  top: 0px;
  bottom: 0px;
  width: 20px;
  background: url(../images/main-nav/cart.svg) no-repeat center/contain;
  filter: grayscale(100%);
}

.main-nav__link--search:hover::before,
.main-nav__link--bucket:hover::before,
.main-nav__link--search:focus::before,
.main-nav__link--bucket:focus::before {
  filter: grayscale(0%);
}

.main-nav__link--search:active::before,
.main-nav__link--bucket:active::before {
  opacity: 0.5;
}

@media (min-width: 768px) {
  .main-nav {
    position: relative;
    top: initial;
    left: initial;
    width: 100%;
    height: auto;
  }

  .main-nav__btn {
    display: none;
  }

  .main-nav__list {
    overflow: visible;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    opacity: 1;
    height: auto;
    background: transparent;
  }

  .main-nav__list.main-nav__list--show {
    animation: initial;
    display: flex;
  }

  .main-nav__item {
    border-bottom: initial;
    flex-basis: 150px;
    flex-grow: 1;
  }

  .main-nav__link.main-nav__link--bottom {
    top: 80%;
    font-size: 1.5rem;
  }

  .main-nav__link--search {
    width: 83px;
    font-size: 0px;
    color: transparent;
    border-left: 1px solid #ececec;
    border-right: 1px solid #ececec;
  }

  .main-nav__link--bucket {
    text-align: right;
  }
  .main-nav__link--bucket::before {
    left: -20px;
  }
}
.contacts {
  margin-bottom: 40px;
}

.contacts__data {
  margin-bottom: 40px;
}

.contacts__list {
  list-style: none;
}

.contacts__item {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 40px;
}
.contacts__item .contacts__name, .contacts__item .contacts__mail, .contacts__item .contacts__address {
  margin-right: 30px;
  font-size: 1.5rem;
  font-weight: normal;
  line-height: 2em;
  color: #444;
}
.contacts__item .contacts__mail, .contacts__item .contacts__address {
  width: 185px;
}
.contacts__item .contacts__mail {
  text-decoration: none;
  border-bottom: 1px solid #62d1ba;
}
.contacts__item .contacts__mail:hover, .contacts__item .contacts__mail:focus {
  color: #62d1ba;
}
.contacts__item .contacts__mail:active {
  opacity: 0.5;
}

.contacts__map {
  position: relative;
  width: 100%;
  height: 460px;
  overflow: hidden;
  margin-bottom: 40px;
}
.contacts__map iframe {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 100%;
}

@media (min-width: 768px) {
  .contacts {
    margin-bottom: 60px;
  }
  .contacts .btn {
    margin-left: auto;
    margin-right: auto;
    max-width: 335px;
  }

  .contacts__data {
    margin-bottom: 20px;
  }
  .contacts__data .block-title {
    position: relative;
  }
  .contacts__data .block-title::after {
    display: block;
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    width: 50px;
    height: 80px;
    background-image: url(../images/contacts/flag.svg);
    background-repeat: no-repeat;
    background-size: cover;
  }

  .contacts__list {
    display: flex;
    align-items: flex-start;
  }

  .contacts__item {
    margin-right: 40px;
    flex-shrink: 0;
  }

  .contacts__map {
    margin-bottom: 60px;
  }
}
.goods {
  padding-top: 35px;
  padding-bottom: 35px;
  margin-bottom: 35px;
  background: url(../images/goods/zigzag-line-desktop.svg);
  background-repeat: repeat-x;
  background-position: bottom;
}

.goods__titles {
  margin-bottom: 30px;
}

.goods__description {
  text-align: left;
  color: #444;
  font-size: 1.5rem;
  line-height: 1.6;
  font-weight: normal;
  margin-bottom: 35px;
}

@media (min-width: 768px) {
  .goods {
    padding-top: 56px;
    margin-bottom: 60px;
  }

  h2.goods__title {
    margin-bottom: 0;
  }

  .goods__titles {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 60px;
  }
}
.price-table {
  border-collapse: collapse;
  margin-bottom: 30px;
  width: 100%;
  text-align: left;
}
.price-table .price-table__row {
  border-bottom: 1px solid #ececec;
}
.price-table .price-table__row td {
  padding-top: 15px;
  padding-bottom: 10px;
}

.order {
  position: relative;
  text-align: center;
  padding-bottom: 165px;
}

.order__img-wrap {
  position: relative;
  width: 100%;
  height: 320px;
  overflow: hidden;
  margin-bottom: 30px;
}
.order__img-wrap img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.order__check {
  position: absolute;
  left: 0;
  bottom: 40px;
  width: 100%;
}

.order__price {
  font-size: 1.7rem;
  line-height: 1.4;
  font-weight: bold;
  color: #444;
  margin-bottom: 28px;
}

@media (min-width: 768px) {
  .order {
    display: flex;
    justify-content: flex-end;
    padding-bottom: 55px;
  }

  .order__wrap {
    max-width: 290px;
    margin-right: 45px;
  }

  .order__check {
    position: relative;
    left: initial;
    bottom: initial;
  }

  .order__img-wrap {
    flex-shrink: 0;
    width: 50%;
    height: 400px;
  }
}
.btn {
  font-family: "Open Sans", sans-serif;
  display: block;
  outline: none;
  background: transparent;
  padding-top: 20px;
  padding-bottom: 20px;
  text-align: center;
  font-size: 1.7rem;
  line-height: 1.2;
  font-weight: bold;
  text-transform: uppercase;
  color: #5d5d5d;
  border: 2px solid #444;
  width: 100%;
  transition: all 0.3s ease-in-out;
}
.btn:hover, .btn:focus {
  color: #fff;
  background: #444;
}
.btn:active {
  color: #7c7c7c;
}

.about-us {
  margin-bottom: 60px;
}

.about-us__list {
  list-style: none;
}

.about-us__item {
  margin-bottom: 30px;
  max-width: 260px;
}

.about-us__img {
  width: 55px;
  margin-left: -5px;
  margin-right: auto;
  text-align: center;
  margin-bottom: 20px;
  overflow: hidden;
}

.about-us__item span {
  font-size: 1.7rem;
  line-height: 1.18;
  font-weight: bold;
}

@media (min-width: 768px) {
  .about-us {
    max-width: 750px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 80px;
  }

  .about-us__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: flex-start;
  }
  .about-us__list about-us__item {
    max-width: 300px;
  }
}
.response {
  position: relative;
  padding-bottom: 45px;
  margin-bottom: 40px;
  background: url(../images/goods/zigzag-line-desktop.svg);
  background-repeat: repeat-x;
  background-position: bottom;
}

.response__list {
  list-style: none;
}

.response__item {
  display: none;
  margin-bottom: 40px;
}
.response__item.response__item--active {
  display: block;
}

.response__item .txt {
  font-size: 1.5rem;
  line-height: 2em;
  font-weight: 500;
  margin-bottom: 70px;
}

.response__buttons {
  display: flex;
  margin-bottom: 35px;
  justify-content: space-between;
}
.response__buttons .btn {
  border: 1px solid #ececec;
}
.response__buttons .btn:focus {
  border: 1px solid #63d1bb;
}
.response__buttons .btn:hover, .response__buttons .btn:focus {
  background: transparent;
}
.response__buttons .btn:hover svg, .response__buttons .btn:focus svg {
  fill: #666;
}
.response__buttons .btn:active svg {
  fill: #b2b2b2;
}

